/**
 * 卡片相关工具函数
 */

import { CARD_IMAGE_BASE } from './constants';
import { cardNameToFileName } from './data/name-mappings';
import type { Rarity } from './types';

/**
 * 获取完整的卡片名称
 * 直接从 RealCard 数据中提取，不需要动态生成
 */
export function getFullCardName(fullName: string): string {
  return fullName;
}

/**
 * 构建卡片图片URL（基于完整卡名）
 * @param fullCardName 完整卡名，如 "【絵空靴】杜野凛世"
 * @param awakened 是否为觉醒卡面（默认false，用于日常展示；true用于技能卡面）
 * @returns 完整的图片URL
 *
 * 使用WebP格式：
 * - 文件体积减少50%（相比PNG）
 * - 加载速度提升2倍
 * - 现代浏览器完全支持（Chrome 23+, Firefox 65+, Safari 14+）
 */
export function buildCardImageUrlFromName(fullCardName: string, awakened: boolean = false): string {
  const fileName = cardNameToFileName(fullCardName, awakened);
  return `${CARD_IMAGE_BASE}/${fileName}.webp`;
}

/**
 * 从角色名提取纯文本（去除【】）
 * @param fullCardName 完整卡名
 * @returns 纯角色名
 */
export function extractCharacterName(fullCardName: string): string {
  const match = fullCardName.match(/】(.+)$/);
  return match ? match[1] : fullCardName;
}

/**
 * 从完整卡名提取主题
 * @param fullCardName 完整卡名
 * @returns 主题
 */
export function extractTheme(fullCardName: string): string {
  const match = fullCardName.match(/【(.+)】/);
  return match ? match[1] : '';
}

/**
 * 构建卡片ID（用于去重判断）
 * @param fullCardName 完整卡名
 * @param rarity 稀有度
 * @returns 卡片ID，如 "【絵空靴】杜野凛世_UR"
 */
export function buildCardId(fullCardName: string, rarity: Rarity): string {
  return `${fullCardName}_${rarity}`;
}

/**
 * 解析卡片ID
 * @param cardId - 卡片ID
 * @returns 解析后的信息
 */
export function parseCardId(cardId: string): {
  fullCardName: string;
  rarity: Rarity;
} | null {
  const parts = cardId.split('_');
  if (parts.length < 2) return null;

  const rarity = parts[parts.length - 1];
  const fullCardName = parts.slice(0, -1).join('_');

  if (!['R', 'SR', 'SSR', 'UR'].includes(rarity)) {
    return null;
  }

  return {
    fullCardName,
    rarity: rarity as Rarity,
  };
}
